/* Base colors */
body {
  font-family: "Montserrat", sans-serif; }

h1, h2, h3, h4, h5, h6 {
  font-family: "Pacifico", cursive;
  margin-top: 0; }

@font-face {
  font-family: 'fontastic';
  src: url("../assets/fonts/fontastic/fontastic.eot");
  src: url("../assets/fonts/fontastic/fontastic.eot?#iefix") format("embedded-opentype"), url("../assets/fonts/fontastic/fontastic.woff") format("woff"), url("../assets/fonts/fontastic/fontastic.ttf") format("truetype"), url("../assets/fonts/fontastic/fontastic.svg#fontastic") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block; }

[data-icon]:before {
  font-family: "fontastic" !important;
  content: attr(data-icon);
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

[class^="icon-"]:before,
[class*=" icon-"]:before {
  font-family: "fontastic" !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.icon-facebook:before {
  content: "\61"; }

.icon-twitter:before {
  content: "\62"; }

.icon-youtube:before {
  content: "\63"; }

.icon-mail:before {
  content: "\64"; }

.icon-location:before {
  content: "\65"; }

.icon-phone:before {
  content: "\66"; }

.icon-menu:before {
  content: "\67"; }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }
  *::before, *::after {
    box-sizing: border-box; }

html,
body {
  height: 100%;
  width: 100%;
  margin: 0; }

body {
  background-color: #FFFFFF;
  font-size: 16px;
  line-height: 1.4; }

a {
  color: #8b0000;
  transition: color .3s linear; }
  a:hover {
    color: #5c4275; }

img {
  max-width: 100%;
  height: auto;
  display: block; }

.page {
  width: 100%;
  height: 100%;
  position: relative; }

.container {
  margin: 0 auto;
  padding: 0 auto;
  position: relative;
  width: 100%;
  background-color: pink; }
  @media screen and (min-width: 768px) {
    .container {
      max-width: 708px;
      background-color: yellow; } }
  @media screen and (min-width: 1084px) {
    .container {
      max-width: 1024px;
      background-color: orange; } }
  @media screen and (min-width: 1920px) {
    .container {
      max-width: 1860px;
      background-color: #FF0000; } }

button, .btn {
  border: none;
  background-color: #FF0000;
  color: #000000;
  padding: 10px 14px;
  cursor: pointer;
  font-size: 13px;
  transition: background-color 0.2s linear; }
  button:hover, .btn:hover {
    background-color: #BC3A80; }

.btn-prev, .bth-next {
  position: relative;
  margin-right: 6px;
  margin-top: -8px;
  z-index: 8; }
